@use "../../../../scss/utils" as *;

.link-back {
   position: relative;
   z-index: 2;
   // display: flex;
   align-items: center;
   gap: 16px;
   font-size: 14px;
   line-height: 120%;
   letter-spacing: -0.05em;
   text-transform: uppercase;
   text-align: right;
   display: none;
   transition: color 0.3s;

   &:hover {
      color: #DB0E0E;

      svg {
         transform: scale(1.3);
      }
   }

   svg,
   img {
      transition: transform 0.3s;
   }

   @media (min-width:768px) {
      display: flex;
   }

   @include size_adaptive (font-size, 14, 18, 1366, 1920);
   @include size_adaptive (font-size, 18, 24, 1920, 2560);

   @media (min-width:2560px) {
      font-size: 24px;
   }
}